.scroll-down {
  position: fixed; /* Keeps the arrow in the viewport */
  bottom: 20px; /* Adjust for spacing from the bottom */
  left: 50%;
  transform: translateX(-50%); /* Centers horizontally */
  text-align: center;
  animation: bounce 2s infinite;
  transition: opacity 0.5s ease-in-out; /* Smooth transition for visibility */
}

.scroll-down svg {
  fill: #fff; /* White color for the arrow */
  width: 36px;
  height: 36px;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-10px);
  }
  60% {
    transform: translateY(-5px);
  }
}

/* Control visibility */
.scroll-down.hidden {
  opacity: 0;
  pointer-events: none; /* Prevents interaction when hidden */
}

.scroll-down.visible {
  opacity: 1;
}
